export default {
  data () {
    return {
      currentIndex: 0
    }
  },
  methods: {
    onSwiperFn (index, parentid, catid) {
      let id = 0
      !parentid ? id = parentid : id = catid
      this.currentIndex = index
      const data = {
        ids: id,
        secondaryIndex: index
      }
      this.$emit('secondaryNavChild', data)
    }
  },
  props: {
    lists: {
      type: Array,
      default: []
    },
    state: {
      type: Boolean,
      default: false
    }
  }
}
